async function getMfgItemDetails(identifier, ItemType, headers) {
  let url = `https://oi000186152-us1-space.3dexperience.3ds.com/enovia/resources/v1/modeler/dsmfg/dsmfg:MfgItem/${identifier}?mask=dsmfg:MfgItemMask.Details`;
  
  try {
    const itemResponse = await callEnoviaWebService("GET", url, "", headers);

    if (itemResponse.status && itemResponse.output) {
      return itemResponse.output;
    } else {
      console.log(`No valid response for identifier ${identifier}`);
      return {}; // Return an empty object if the response is invalid
    }
  } catch (error) {
    console.error(`Error fetching item details for ${identifier}:`, error);
    return {}; // Return an empty object on error
  }
}

async function getLatestRevision(identifier, type, headers) {
  const revurl = `https://oi000186152-us1-space.3dexperience.3ds.com/enovia/resources/v1/modeler/dslc/version/getGraph`;
  const ret = "";
  try {
    const relativePath =
      (type === "CreateAssembly" || type === "Provide")
        ? `/resources/v1/modeler/dsmfg/dsmfg:MfgItem/${identifier}`
        : `/resources/v1/modeler/dseng/dseng:EngItem/${identifier}`;

    const Body = {
      data: [
        {
          id: identifier,
          identifier: identifier,
          type: type,
          source: "https://oi000186152-us1-space.3dexperience.3ds.com/enovia",
          relativePath: relativePath,
        },
      ],
    };

    // Make the API call with the dynamically created body
    const response = await callEnoviaWebService(
      "POST",
      revurl,
      JSON.stringify(Body),
      headers
    );

    // Check if the response contains status and output properties
    if (response.status && response.output) {
      // Loop through each result in the response and check for ancestors
      for (const result of response.output.results) {
        if (
          result.ancestors &&
          result.ancestors.some(
            (ancestor) => ancestor.identifier === identifier
          )
        ) {
          return result.id;
        }
      }
    } else {
      console.error(
        "API response does not contain the expected 'status' and 'output'."
      );
      return ret;
    }
  } catch (error) {
    console.error(`Error fetching getLatestRevision for ${identifier}:`, error);
    return ret;
  }
}
async function getScopedItms(identifier, headers) {
  let url = "";

  url = `https://oi000186152-us1-space.3dexperience.3ds.com/enovia/resources/v1/modeler/dsmfg/dsmfg:MfgItem/${identifier}/dsmfg:ScopeEngItem`;

  try {
    const itemResponse = await callEnoviaWebService("GET", url, "", headers);

    if (itemResponse.status && itemResponse.output) {
      return {
        identifier: itemResponse.output.member[0].ScopeEngItem.identifier,
        type: itemResponse.output.member[0].ScopeEngItem.type,
      };
    } else {
      console.log(`No valid response for identifier ${identifier}`);
      return {}; // Return an empty object if the response is invalid
    }
  } catch (error) {
    console.error(`Error fetching item details for ${identifier}:`, error);
    return {}; // Return an empty object on error
  }
}

async function expandItms(identifier, headers) {
  let url = `https://oi000186152-us1-space.3dexperience.3ds.com/enovia/resources/v1/modeler/dsmfg/dsmfg:MfgItem/${identifier}/expand?$mask=dsmfg:MfgItem.ExpandMask.Default.V1`;
  const body = {
    "expandDepth": 1,
    "withPath": false
  };
  try {
    const expandResponse = await callEnoviaWebService("POST", url, body, headers);
    console.log("expandItms --1-- ",expandResponse?.output);
    if (expandResponse.status && expandResponse.output) {
      const expandItmArray = [];
      const totalSize = expandResponse?.output?.totalItems;
      // if(totalSize > 1){
        for (let i = 0; i < totalSize; i++) {
          const item = expandResponse?.output?.member[i];
          const jsonObject = {
            id: item.id,
            type: item.type,
            title: item.title
          };
          if(item.type === "CreateAssembly" || item.type === "Provide")
            expandItmArray.push(jsonObject);
        }
      //}
      console.log("expandItmArray ---- ",expandItmArray);
      return {expandItmArray};
    } else {
      console.log(`No valid response for identifier ${identifier}`);
      return {}; // Return an empty object if the response is invalid
    }
  } catch (error) {
    console.error(`Error fetching item details for ${identifier}:`, error);
    return {}; // Return an empty object on error
  }
}


async function getPrevAndNextSeqMCO(identifier, headers, mcoName) {
  let prevSeqMCO = "";
  let nextSeqMCO = "";
  let MCOSeq = "";
  console.log("mcoName", mcoName)
  let className = mcoName.split('-')[2];
  console.log("className in PREV Sequence",className)
  //Get Scoped eng item
  const engItemDetails = await getScopedItms(identifier, headers);
        if(engItemDetails){
          let EngItemId = engItemDetails.identifier;
          //get classification attributes for the eng item from class className
          const classAttr = await getAssignedClasses(EngItemId, headers, className);
          //From classAttr get the class details whose name is matching with the suffix of dropped MCO
          //get the PreviousSequence attribute from classAttr
          // Format of values of attribute PreviousSequence -  MCONAME-TEMPLATE-MBOM-SEQ-ADDSeq,  MCONAME2-TEMPLATE2-MBOM-SEQ2-ADDSeq2
          // For example, PreviousSequence = MCO-00005693-MVO-OracleTemplate-True-2-1, MCO-00008893-MVO-OracleTemplate-True-3-2, MCO-00009563-MVO-OracleTemplate-True-4-3
              
             /*  Case 1: When the current MCO Name matches MCOName
              Check if the "Previous Sequence" attribute is not empty:
                If not empty:
                  Fetch the latest MCO and Sequence values from the Previous Sequence attribute. 
                  This is value of prev seq MCO & Sequence.
                If empty:
                  Look into the previous revision to get its MCO Name and Sequence. This is value of prev seq MCO & Sequence.
                  If previous revision does not exist, then set orev seq MCO Name and Sequence as empty.
          */
        /*     Case 2: When the current MCO Name is present in the "Previous Sequence" attribute
                  1.	Split the Previous Sequence attribute (comma-separated string).
                  2.	Search the list for a match to the current MCO.
                  3.	If found:
                    o	Set previous MCO and sequence from this attribute.
                  4.	If not found:
                    o	Check the previous revision & get the MCO Name and Sequence. This is value of prev seq MCO & Sequence.
                  5.	For the next MCO and sequence:
                    o	Split the Previous Sequence attribute.
                    o	Try to find the next MCO after the current MCO in the list.
                    o	If found, 
                      use it as the next MCO and sequence.
                    o	If not found:
                      	If next revision exist
                        	Check the next revision's Previous Sequence attribute.
                        	If prev seq not empty, take the first value from it as the next MCO and sequence.
                        	If prev seq is empty, get MCOName & sequence as next MCO and sequence.
                      	If next rev does not exist
                        	next seq MCO and sequence will be empty.
                  */
            }
          

  let url = `https://oi000186152-us1-space.3dexperience.3ds.com/enovia/resources/v1/modeler/dslc/version/getGraph`;
  const body = {
      "data": [
        {
          "id": identifier,
          "identifier": identifier,
          "type": "CreateAssembly",
          "source": "https://oi000186152-us1-space.3dexperience.3ds.com/enovia",
          "relativePath": `/resources/v1/modeler/dsmfg/dsmfg:MfgItem/${identifier}`
        }
      ]
  };
  try {
    const expandResponse = await callEnoviaWebService("POST", url, body, headers);
    console.log("getPrevSeqMCO --1-- ",expandResponse?.output);
    if (expandResponse.status && expandResponse.output) {
      const ItemId = await findPreviousRevisionIdentifier(expandResponse.output, identifier);
      if(ItemId){
        const engItemDetails = await getScopedItms(ItemId, headers);
        if(engItemDetails){
          let EngItemId = engItemDetails.identifier;
          const classAttr = await getAssignedClasses(EngItemId, headers);
          const prevSeqMCO = classAttr
            .map((item) => item?.MCOName)
            .filter((name) => name)
            .join(", ");
          console.log("Prev seq MCO is:", prevSeqMCO);
          if(prevSeqMCO){
          const lastHyphenIndex = prevSeqMCO.lastIndexOf("-");
          const firstHyphenIndex = prevSeqMCO.indexOf("-");
          let CAName = "";
          if (lastHyphenIndex !== -1) {
            CAName = prevSeqMCO.substring(firstHyphenIndex+1, lastHyphenIndex);
          } else {
            CAName = prevSeqMCO;
          }
          //const MCODetails = await SearchCA(CAName, headers);
          //console.log("MCODetails --475-- ",MCODetails);
          return prevSeqMCO;
          }else{
            return undefined;
          }
        } else {
          console.log(`scope does NOT exist for identifier ${identifier}`);
          return undefined; // Return an empty object if the response is invalid
        }
      } else {
        console.log(`Prev revision does NOT exist for identifier ${identifier}`);
        return undefined; // Return an empty object if the response is invalid
      }
    } else {
      console.log(`No valid response for identifier ${identifier}`);
      return undefined; // Return an empty object if the response is invalid
    }
  } catch (error) {
    console.error(`Error fetching item details for ${identifier}:`, error);
    return undefined; // Return an empty object on error
  }
}

function findNextRevisionIdentifier(data, targetIdentifier) {
  if (!data?.results?.length) return null;
  const versions = data.results[0]?.versions || [];
  // Step 1: Find the current revision
  const currentItem = versions.find(v => v.identifier === targetIdentifier);
  if (!currentItem) return null;
  const currentRevision = currentItem.revision;
  // Step 2: Determine the revision format and compute the next one
  const nextRevision = getNextRevision(currentRevision);
  // Step 3: Find the version with that revision
  const nextItem = versions.find(v => v.revision === nextRevision);
  return nextItem ? nextItem.identifier : null;
}

// Helper to determine revision format and compute next revision
function getNextRevision(revision) {
  // Numeric (e.g. '01', '02', or '1')
  if (/^\d+$/.test(revision)) {
    const numeric = parseInt(revision, 10) + 1;
    return revision.length > 1
      ? String(numeric).padStart(revision.length, '0') // preserve leading zeroes
      : String(numeric);
  }
  // Alphabetic (e.g. 'AA', 'AB', 'AC', ..., 'AZ', 'BA', etc.)
  if (/^[A-Z]+$/.test(revision)) {
    return incrementAlphaRevision(revision);
  }
  // If format is unknown, return as is
  return revision;
}

// Increment alphabetic revision like AA  AB, AZ  BA
function incrementAlphaRevision(str) {
  let chars = str.split('');
  let i = chars.length - 1;
  while (i >= 0) {
    if (chars[i] === 'Z') {
      chars[i] = 'A';
      i--;
    } else {
      chars[i] = String.fromCharCode(chars[i].charCodeAt(0) + 1);
      return chars.join('');
    }
  }
  // All characters were 'Z', so prepend 'A' (e.g., 'ZZ'  'AAA')
  return 'A' + chars.join('');
}


function findPreviousRevisionIdentifier(data, targetIdentifier) {
  if (!data?.results?.length) return null;
  const versions = data.results[0]?.versions || [];

  // Step 1: Find the current revision
  const currentItem = versions.find(v => v.identifier === targetIdentifier);
  if (!currentItem) return null;
  const currentRevision = currentItem.revision;

  // Step 2: Determine the revision format and compute the previous one
  const previousRevision = getPreviousRevision(currentRevision);
  if (previousRevision === null) { // Handle the case where there's no previous revision (e.g., 'A' or '01')
    return null;
  }

  // Step 3: Find the version with that previous revision
  const previousItem = versions.find(v => v.revision === previousRevision);
  return previousItem ? previousItem.identifier : null;
}

// Helper to determine revision format and compute the previous revision
function getPreviousRevision(revision) {
  // Numeric (e.g., '01', '02', or '1')
  if (/^\d+$/.test(revision)) {
    const numeric = parseInt(revision, 10);
    // If it's the first numeric revision (e.g., '0' or '00', or '1'), there's no previous
    if (numeric <= 1) { // Assuming revisions start at 1 or 01. Adjust if your system starts at 0.
      return null;
    }
    const previousNumeric = numeric - 1;
    // Preserve leading zeroes if original had them
    return revision.length > 1
      ? String(previousNumeric).padStart(revision.length, '0')
      : String(previousNumeric);
  }

  // Alphabetic (e.g., 'AA', 'AB', 'AC', ..., 'AZ', 'BA', etc.)
  if (/^[A-Z]+$/.test(revision)) {
    return decrementAlphaRevision(revision);
  }

  // If format is unknown, or if it's the very first revision in that format, return null
  return null;
}

// Decrement alphabetic revision like AB  AA, BA  AZ
function decrementAlphaRevision(str) {
  let chars = str.split('');
  let i = chars.length - 1;

  while (i >= 0) {
    if (chars[i] === 'A') {
      // If 'A' and it's the first character, we can't decrement further (e.g., 'A')
      if (i === 0) {
        return null; // No previous alphabetic revision (e.g., 'A' has no '0' or prior alpha)
      }
      chars[i] = 'Z'; // Wrap around 'A' to 'Z'
      i--; // Move to the previous character
    } else {
      chars[i] = String.fromCharCode(chars[i].charCodeAt(0) - 1); // Decrement character
      // If all leading characters became 'Z' and it was e.g. 'AAA', it should become 'ZZ'
      // This is handled implicitly by the while loop and join, e.g. 'BA' -> 'AZ' will work if previous was A and wraps.
      return chars.join('');
    }
  }

  // This part is reached if all characters were 'A' (e.g., "A", "AA", "AAA")
  // and it indicates there's no logical previous alphabetic string in the sequence.
  // The first 'if (i === 0)' check already handles the single 'A' case.
  // For 'AA' -> 'A' this is complex and often not desired in revision systems.
  // Based on the 'incrementAlphaRevision' logic, 'ZZ' goes to 'AAA'.
  // To reverse 'AAA' to 'ZZ', we need to remove the first 'A' and make rest 'Z'
  if (chars.length > 1 && chars[0] === 'A' && chars.every((char, index) => index === 0 || char === 'A')) {
      return 'Z'.repeat(chars.length - 1); // e.g., "AAA" -> "ZZ"
  }

  return null; // Should ideally not be reached for standard cases, but as a fallback.
}

export const SearchCA = async (flowDownCA, headers) => {
  let CADetails = { CAAtt: [] };
  console.log("We are into Search CA Details");
  const ENOVIA_BASE_URL = process.env.REACT_APP_ENOVIA_BASE_URL;
  try {
    // 1st API call to search for the Change Action
    let urlObjWAF = `${ENOVIA_BASE_URL}/resources/v1/modeler/dslc/changeaction/search?$searchStr=name:MCO-*${flowDownCA}`;
    const searchResponse = await callEnoviaWebService("GET", urlObjWAF, "", headers);
    const caID = searchResponse.output.changeAction[0]?.identifier;
    console.log("Response From 1st Call", caID);

    if (caID) {
      // 2nd API call to fetch Change Action details
      const CAUrl = `${ENOVIA_BASE_URL}/resources/v1/modeler/dslc/changeaction/${caID}?$fields=proposedChanges,flowDown`;
      console.log("CAUrl --- "+CAUrl);
      const CAresponse = await callEnoviaWebService("GET", CAUrl, "", headers);
      console.log("Response From 2nd Call", CAresponse);

      if (CAresponse) {
        // Use for...of for async handling in loop
        for (const item of CAresponse.isFlowDownOf || []) {
          if (item.type === "Change Action") {
            const parentCAUrl = `${ENOVIA_BASE_URL}/resources/v1/modeler/dslc/changeaction/${item.identifier}?$fields=proposedChanges,flowDown`;
            console.log("parentCAUrl --- "+parentCAUrl);
            try {
              // 3rd API call to fetch Parent Change Action details
              const parentCAResponse = await callEnoviaWebService("GET", parentCAUrl, "", headers);

              console.log("Response From 3rd Call", parentCAResponse);

              if (parentCAResponse) {
                CADetails.CAAtt.push({
                  CATitle: parentCAResponse.title,
                  CAState: parentCAResponse.state,
                });
              }
            } catch (error) {
              console.error("Error fetching parent CA data:", error);
            }
          }
        }
        CADetails["MCOState"] = CAresponse.state;
        CADetails["MCOTitle"] = CAresponse.title;
      }
    }
    console.log("Final CA Details", CADetails);
    return CADetails;
  } catch (error) {
    console.error("Error in SearchCA:", error);
    throw error;
  }
};

